import IndexList from '../components/bscroll/index-list.vue'
import { AjaxHttp } from '../../../js/ajaxHttp'
import { callPhone, getImageUrl, isNullValue } from "../../../js/utility.js"
import { mapState } from 'vuex'

export default {
  components: {
    IndexList
  },
  data() {
    return {
    }
  },
  computed: {
    ...mapState([
      'detail',
      'addressBook'
    ])
  },
  mounted() {
    if (this.addressBook.length === 0) {
      this.getInfos();
    }
  },
  methods: {
    selectItem(item) {
      this.$router.push({
        name: 'user',
        params: {
          id: item.value
        }
      })
      // console.log(item.name + item.value);
    },
    getInfos() {
      AjaxHttp('POST', 'm=user&a=contacts', {}, this, function(res, wself, success) {
        if (success) {

          let ret = [];
          res.data.user_list.forEach((contactsGroup) => {
            let group = {};
            group.name = contactsGroup.name;
            group.items = [];
            contactsGroup.role_array.forEach((contact) => {
              let item = {};
              item.name = contact.full_name;
              item.role_name = contact.role_name;
              item.department_name = isNullValue(contact.department_name) ? '' : contact.department_name;
              item.telephone = contact.telephone;
              item.thumb_path = getImageUrl(contact.thumb_path);
              item.value = contact.role_id;
              group.items.push(item);
            });
            ret.push(group);
          })
          wself.$store.commit('updateAddressBook', ret);
          // wself.addressBook = ret;
          window.lal = wself.$refs.lal;
        }
      });
    },
    clickTitle(title) {
      // console.log(title);
    }
  }
}




